.list{
    color:#1A1A1D;
    width:100%;
    background-color:#41B3A3;
    height:100px;
    margin:5% ;
    
    
    border-radius:5px;
}

.list p{
padding-top: 2%;
   padding-left:5%;
   position:relative;
   font-weight: bold;
   display:flexbox;
   
}

.list p span{
    position:absolute;
    right:5%;
    cursor:pointer;
    
}
.list p input{
    background-color: transparent;
    border:0;
    color:#1A1A1D;
    font-size:20px;
    width:80%;
    
    
}
.list p input:focus{
    outline:none;
}
#dateid{
    margin-top:10px;
    color:#C3073F;

}

@media(max-width:500px){
    .list {
        color: #1A1A1D;
        width: 100%;
        background-color: #41B3A3;
        height: 50px;
        margin: 2%;
        border-radius: 5px;
      }
      .list p input {
        background-color: transparent;
        border: 0;
        color: #1A1A1D;
        font-size: 15px;
        width: 80%;
    }
    .svg-inline--fa {
        display: inline-block;
        font-size: inherit;
        height: 3em;
        overflow: visible;
        vertical-align: -0.125em;
    }

    .svg-inline--fa.fa-w-14 {
        width: 0.600em;
    }
    #dateid {
        margin-top: 2px;
        color: #C3073F;
        font-size: 9px;
    }

}
@media(min-width:600px){
    .list {
        color: #1A1A1D;
        width: 80%;
        background-color: #41B3A3;
        height: 80px;
        margin: 5%;
        border-radius: 5px;
    }
    .svg-inline--fa.fa-w-14 {
        width: 1.5em;
    }
    .svg-inline--fa {
        display: inline-block;
        font-size: inherit;
        height: 4em;
        overflow: visible;
        vertical-align: -0.125em;
    }
  
}