body{
  background-color:#1A1A1D;
}

.App{
  background-color:#240090;
  min-height: 500px ;
  width:60% ;
  margin: 5% auto;
  padding:5%;
  border-radius:30px;
  
}
#title{
  position:relative;
  background-color:#e51e2a;
  color:#66FCF1;
  text-align: center;
  justify-content: center;
  margin:2% auto;
  font-size:50px;
  
}
#todolist input{
  background-color:#C5C6C7;
  border:0;
  width:60%;
  height:50px;
  padding:0 20px;
  font-size: 20px;
  border-radius:10px;
  justify-items: center;
  margin-left: auto;
  margin-right: auto;

  
  color:#0B0C10;
  
  

}
#todolist{
  display:flex;
  flex-direction: row;

}
#todolist input::placeholder{
  color:#1A1A1D;
  font-weight:bold;
}
#todolist input:focus{
  outline:none;
}

#todolist button{
  position: relative;
  margin: auto;
  height:40px;
  width:10%;
  border-radius: 20px;
  border:0;
  background-color:#e51e2a;
  cursor:pointer;
  font-weight:bolder;
  color:#fff;
  font-size: 15px;
  
  

}
#todolist button:hover{
  background:#8EE4AF;
  color:black;
  transform: translateY(-10px);
}
#todolist button:focus{
  outline:none;
}

#date{
  color:#C5C6C7;
  margin-left:5%;
  font-size:25px;
}

@media(max-width:500px){
  #title {
    position: relative;
    background-color: #e51e2a;
    color: #66FCF1;
    text-align: center;
    justify-content: center;
    margin-top: 2%;
    font-size: 25px;
}
#date {
  color: #C5C6C7;
  margin-left: 5%;
  font-size: 20px;
}
#todolist button:hover {
  background: #8EE4AF;
  color: black;
  transform: translateX(5px);
}
#todolist {
  display: flex;
  flex-direction: column;
}
#todolist input {
  background-color: #C5C6C7;
  border: 0;
  width: 60%;
  height: 50px;
  padding: 0 20px;
  font-size: 15px;
  border-radius: 10px;
  justify-items: center;
  margin-left: auto;
  margin-right: auto;
  color: #0B0C10;

}

#todolist button {
  position: relative;
  margin: 5px auto;
  height: 30px;
  width: 25%;
  border-radius: 10px;
  border: 0;
  background-color: #e51e2a;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
  font-size: 15px;
}
}

